@import 'sohne/sohne';
@import 'icomoon/icomoon';

:root {
    --cf-scale-0: 0rem;
    --cf-scale-1: 0.4rem;
    --cf-scale-1-5: 0.6rem;
    --cf-scale-2: 0.8rem;
    --cf-scale-2-5: 1.0rem;
    --cf-scale-3: 1.2rem;
    --cf-scale-4: 1.6rem;
    --cf-scale-4-5: 1.7rem;
    --cf-scale-5: 1.8rem;
    --cf-scale-6: 2rem;
    --cf-scale-7: 2.2rem;
    --cf-scale-8: 2.4rem;
    --cf-scale-9: 3.8rem;
    --cf-scale-10: 5.2rem;
    --cf-scale-11: 6.6rem;
    --cf-scale-12: 8rem;
    --cf-scale-3-5: 1.4rem;
    --cf-theme-text-hr-span-after: rgba(255, 255, 255, 0.3);
}

@media (min-width: 768px) {
    :root {
        --cf-scale-0: 0rem;
        --cf-scale-1: 0.4rem;
        --cf-scale-1-5: 0.6rem;
        --cf-scale-2: 0.8rem;
        --cf-scale-2-5: 1.0rem;
        --cf-scale-3: 1.2rem;
        --cf-scale-4: 1.6rem;
        --cf-scale-5: 1.9rem;
        --cf-scale-6: 2.2rem;
        --cf-scale-7: 2.5rem;
        --cf-scale-8: 2.8rem;
        --cf-scale-9: 4.3rem;
        --cf-scale-10: 5.8rem;
        --cf-scale-11: 7.3rem;
        --cf-scale-12: 8.8rem;
        --cf-theme-text-hr-span-after: rgba(255, 255, 255, 0.3);
    }
}

@media (min-width: 960px) {
    :root {
        --cf-scale-0: 0rem;
        --cf-scale-1: 0.4rem;
        --cf-scale-1-5: 0.6rem;
        --cf-scale-2: 0.8rem;
        --cf-scale-2-5: 1.0rem;
        --cf-scale-3: 1.2rem;
        --cf-scale-4: 1.6rem;
        --cf-scale-5: 2rem;
        --cf-scale-6: 2.4rem;
        --cf-scale-7: 2.8rem;
        --cf-scale-8: 3.2rem;
        --cf-scale-9: 4.8rem;
        --cf-scale-10: 6.4rem;
        --cf-scale-11: 8rem;
        --cf-scale-12: 9.6rem;
        --cf-theme-text-hr-span-after: rgba(255, 255, 255, 0.3);
    }
}

:root,.cf-theme-dark {
    color: var(--cf-theme-text);
    --cf-theme-background: #000;
    --cf-theme-text: #fff;
    --cf-theme-text-hr-span-after: rgba(255, 255, 255, 0.3);
    --cf-theme-separator: rgba(255, 255, 255, 0.3);
    --cf-theme-border-contrast: rgba(255, 255, 255, 0.5);
    --cf-theme-border-contrast-hover: #fff;
    --cf-theme-applepay-border: none;
    --cf-theme-badge-bg: #fff;
    --cf-theme-badge-text: #000;
    --cf-theme-badge-dim-bg: #272c33;
    --cf-theme-badge-dim-text: #fff;
    --cf-theme-carousel-dot: #596170;
    --cf-theme-carousel-dot-active: #fff;
    --cf-theme-dropdown-backdrop: rgba(0, 0, 0, 0.9);
    --cf-theme-dropdown-border: #272c33;
    --cf-theme-dropdown-bg: #191c21;
    --cf-theme-dropdown-shadow: #000;
    --cf-theme-dropdown-hover-bg: #272c33;
    --cf-theme-tooltip-bg: #272c33;
    --cf-theme-tooltip-border: #596170;
    --cf-theme-tooltip-arrow: #596170;
    --cf-theme-tooltip-arrow-after: #272c33;
    --cf-theme-checkbox-bg: #191c21;
    --cf-theme-checkbox-border: #272c33;
    --cf-theme-checkbox-checked: #fff;
    --cf-theme-checkbox-active-border: #596170;
    --cf-theme-checkbox-dis-bg: #f3f3f3;
    --cf-theme-checkbox-dis-border: #dbdde1;
    --cf-theme-checkbox-dis-active-border: #272c33;
    --cf-theme-form-bg: #191c21;
    --cf-theme-form-border: #272c33;
    --cf-theme-form-elem-color: #fff;
    --cf-theme-form-elem-placehold-color: rgba(255, 255, 255, 0.5);
    --cf-theme-form-dis-bg: #272c33;
    --cf-theme-form-dis-border: none;
    --cf-theme-form-dis-active: none;
    --cf-theme-form-dis-elem: rgba(255, 255, 255, 0.5);
    --cf-theme-form-append: rgba(255, 255, 255, 0.3);
    --cf-theme-radio-bg: #191c21;
    --cf-theme-radio-border: #272c33;
    --cf-theme-radio-checked: #fff;
    --cf-theme-radio-dis-bg: var(--cf-theme-radio-bg);
    --cf-theme-radio-dis-border: none;
    --cf-theme-select-menu-bg: #191c21;
    --cf-theme-select-menu-border: #272c33;
    --cf-theme-select-option-bg: #272c33
}

.cf-theme-light,.cf-invert {
    color: var(--cf-theme-text);
    --cf-theme-background: #fff;
    --cf-theme-text: #000;
    --cf-theme-text-hr-span-after: rgba(0, 0, 0, 0.3);
    --cf-theme-separator: rgba(0, 0, 0, 0.3);
    --cf-theme-border-contrast: rgba(0, 0, 0, 0.5);
    --cf-theme-border-contrast-hover: #000;
    --cf-theme-applepay-border: inset 0 0 0 1px #000;
    --cf-theme-badge-bg: #272c33;
    --cf-theme-badge-text: #fff;
    --cf-theme-badge-dim-bg: #dbdde1;
    --cf-theme-badge-dim-text: #000;
    --cf-theme-carousel-dot: rgba(0, 0, 0, 0.6);
    --cf-theme-carousel-dot-active: #000;
    --cf-theme-dropdown-border: #f3f3f3;
    --cf-theme-dropdown-bg: #fff;
    --cf-theme-dropdown-hover-bg: #f3f3f3;
    --cf-theme-tooltip-bg: #fff;
    --cf-theme-tooltip-border: #f3f3f3;
    --cf-theme-tooltip-arrow: #f3f3f3;
    --cf-theme-tooltip-arrow-after: #fff;
    --cf-theme-checkbox-bg: #fff;
    --cf-theme-checkbox-border: #949aa4;
    --cf-theme-checkbox-checked: #272c33;
    --cf-theme-checkbox-active-border: #596170;
    --cf-theme-checkbox-dis-bg: #f3f3f3;
    --cf-theme-checkbox-dis-border: #dbdde1;
    --cf-theme-checkbox-dis-active-border: #dbdde1;
    --cf-theme-form-bg: #fff;
    --cf-theme-form-border: #949aa4;
    --cf-theme-form-elem-color: #000;
    --cf-theme-form-elem-placehold-color: rgba(0, 0, 0, 0.3);
    --cf-theme-form-dis-bg: #f3f3f3;
    --cf-theme-form-dis-border: inset 0 0 0 1px #dbdde1;
    --cf-theme-form-dis-active: #dbdde1;
    --cf-theme-form-dis-elem: rgba(0, 0, 0, 0.3);
    --cf-theme-form-append: rgba(0, 0, 0, 0.3);
    --cf-theme-radio-bg: #fff;
    --cf-theme-radio-border: #949aa4;
    --cf-theme-radio-checked: #000;
    --cf-theme-radio-dis-bg: #f3f3f3;
    --cf-theme-radio-dis-border: inset 0 0 0 1px #dbdde1;
    --cf-theme-select-menu-bg: #fff;
    --cf-theme-select-menu-border: #f3f3f3;
    --cf-theme-select-option-bg: #f3f3f3
}

.container {
    width: 100%;
    padding-right: 8px;
    padding-left: 8px;
    margin-right: auto;
    margin-left: auto
}

@media (min-width: 576px) {
    .container {
        max-width:100%
    }
}

@media (min-width: 768px) {
    .container {
        max-width:100%
    }
}

@media (min-width: 960px) {
    .container {
        max-width:100%
    }
}

@media (min-width: 1200px) {
    .container {
        max-width:1328px
    }
}

.container-fluid {
    width: 100%;
    padding-right: 8px;
    padding-left: 8px;
    margin-right: auto;
    margin-left: auto
}




@media only screen and (min-width: 576px) {
    .container,.container-fluid {
        padding:0 32px
    }
}

@media only screen and (min-width: 768px) {
    .container,.container-fluid {
        padding:0 32px
    }
}

@media only screen and (min-width: 960px) {
    .container,.container-fluid {
        padding:0 64px
    }
}

@media only screen and (min-width: 1200px) {
    .container,.container-fluid {
        padding:0 64px
    }

    .row {
        margin: -16px
    }

    .col,[class^="col-"],[class*=" col-"] {
        padding: 16px
    }

    .small-gutters {
        margin: -8px
    }

    .small-gutters .col,.small-gutters [class^="col-"],.small-gutters [class*=" col-"] {
        padding: 8px
    }
}

